
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #151821;
    color: #949494;
    height: 65px;
}

.navbar-left {
    display: flex;
}

.picture {
    height: 40px;
    filter: invert(1);
}


.navbar-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.navbar-links {
    display: flex;
    align-items: center;
    gap: 10px;
}

.navbar-link {
    text-decoration: none;
    color: #949494;
    font-size: 15px;
    padding: 8px;
}

.navbar-link:hover, .navbar-link:focus {
    color: #56A095;
    font-weight: bold;
}

.navbar-separator {
    color: #56A095;
}
  